import { Injectable, isDevMode } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Subject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SignalKClient {

	private _connect; 			
    public onConnect;	 		
	private _close; 			
    public onClose;	 		    
	private _error; 			
    public onError;	 		    
	private _message; 			
    public onMessage;	 		  
    private ws;                           
    
    private hostname: string;
    private port: number;
    private protocol: string;
    private wsProtocol: string;

    private _version: string= 'v1';      // ** default Signal K api version
    private _filter= null;               // ** id of vessel to filter delta messages
    private _wsTimeout= 20000;           // ** websocket connection timeout
    private _token: string;             // token for when security is enabled on the server

    // ** server information block **
    private server= {
        authRequired: false,
        endpoints: {},
        info: {},
        apiVersions: [],
        ws: { self: null, roles: {} }
    }

    private debug(val: any) { if(isDevMode()){ console.log(val) } }

    constructor( private http: HttpClient ) { 
        this._connect= new Subject<string>();
        this.onConnect= this._connect.asObservable();   
        this._close= new Subject<string>();
        this.onClose= this._close.asObservable();  
        this._error= new Subject<string>();
        this.onError= this._error.asObservable();  
        this._message= new Subject<string>();
        this.onMessage= this._message.asObservable();   
        this.init();                          
    }

    // **************** Signal K API VERSION ***************************

    // ** get / set Signal K preferred api version to use **
    get version():number { return parseInt( this._version.slice(1) ) }

    set version(val: number) {
        let v:string= 'v'+ val;
        if(this.server.apiVersions.length==0) { 
            this._version= v;
            this.debug(`Signal K api version set to: ${v}`);
        }
        else {
            this._version= (this.server.apiVersions.indexOf(v)!=-1) ? v : this._version;
            this.debug(`Signal K api version set request: ${v}, result: ${this._version}`);
        }
    }
    // ** get list of api versions supported by server
    get apiVersions() { return this.server.apiVersions }

    // **************** CONNECTION  ***************************

    // ** is Auth required for this server **
    get authRequired() { return this.server.authRequired }

    // ** set auth token value **
    set authToken(val:string) { this._token= val }

    // ** get auth token for supplied user details **
    login(username:string, password:string) {
        let headers= new HttpHeaders().set('Content-Type', `application/json`);
        return this.http.post(
            `${this.protocol}://${this.hostname}:${this.port}/login`,
            { "username": username, "password": password },
            { headers }
        );
    }

    // ** get / set websocket connection timeout 3000<=timeout<=60000 **
    get connectionTimeout():number { return this._wsTimeout }

    set connectionTimeout(val: number) {
        this._wsTimeout= (val<3000) ? 3000 : (val>60000) ? 60000 : val;
    }    

    // ** initialise client connection settings
    private init(hostname:string=null, port:number=null, useSSL:boolean=false) {
        if(isDevMode()) { 
            hostname= hostname || '192.168.99.100';
            port= port || 3000;  
        }
        else { hostname= hostname || 'localhost' }

        this.hostname = hostname;
        if(useSSL) {
            this.protocol = 'https';
            this.wsProtocol = 'wss';
            this.port = port || 443;
        } 
        else {
            this.protocol = 'http';
            this.wsProtocol = 'ws';
            this.port = port || 80;
        }        
    }

    // ** Signal K server discovery request (/signalk).  
    hello(hostname:string=null, port:number=null, useSSL:boolean=false) {
        this.init(hostname, port, useSSL);
        this.get('/loginStatus').subscribe( r=> {
            this.server.authRequired=(r['authenticationRequired']) ? true : false;
        })
        return this.get('/signalk');
    }    
    
    // ** connect to server
    connect(hostname:string=null, port:number=null, useSSL:boolean=false, subscribe:string=null) {
        this.debug('Contacting Signal K server.........');
        this.hello(hostname, port).subscribe(    // ** discover endpoints **
            response=> {
                this.server.endpoints= (response['endpoints']) ? response['endpoints'] : {};
                this.server.info= (response['server']) ? response['server'] : {};
                this.server.apiVersions= (this.server.endpoints) ? Object.keys(this.server.endpoints) : [];
                this.debug(this.server.endpoints);
                
                // ** connect to stream api at preferred version else fall back to default version
                let url;
                if(this.server.endpoints[this._version] && this.server.endpoints[this._version]['signalk-ws']) {
                    this.debug(`Connecting endpoint version: ${this._version}`);
                    url= `${this.server.endpoints[this._version]['signalk-ws']}`;
                }
                else if(this.server.endpoints['v1'] && this.server.endpoints['v1']['signalk-ws']) { 
                    this.debug(`Connection falling back to: v1`);
                    url= `${this.server.endpoints['v1']['signalk-ws']}` 
                }
                else { 
                    this._error.next( new Error('No Signal K endpoints found!') );
                    return;
                }
                // ** subscribe **
                if(subscribe && ['self','all','none'].indexOf(subscribe)!=-1) { 
                    url+=`?subscribe=${subscribe}`;
                } 
                if(this._token) { url+= `&token=${this._token}` }               
                this.debug(`Connecting to ${url}`);
                this.connectDeltaByUrl(url);
            },
            error=> { 
                this.server.endpoints= {};
                this.server.info= {};
                this.server.apiVersions= [];            
                return this._error.next( error );
            }
        );
    }  

    // ** connect to delta stream with no endpoint discovery
    connectDelta(hostname:string=null, port:number=null, useSSL:boolean=false, subscribe:string=null) {
        this.debug('ConnectDelta.........');
        this.init(hostname, port, useSSL);
        let url= `${this.wsProtocol}://${this.hostname}:${this.port}/signalk/${this._version}/stream`;
        // ** subscribe **
        if(subscribe && ['self','all','none'].indexOf(subscribe)!=-1) { 
            url+=`?subscribe=${subscribe}`;
        } 
        if(this._token) { url+= `&token=${this._token}` }
        this.debug(`Connecting to delta stream at ${url}`);
        this.connectDeltaByUrl(url);
    }  

	// ** connect to delta stream at provided url
	private connectDeltaByUrl(url) {
        if(this.server.authRequired && !this._token) {
            this.debug('Auth Required and NO token available!');
            this._error.next(new Error('Auth Required and NO token available!'));
        }
        if(this.ws) { this.disconnect() }
        this.ws = new WebSocket(url);
        // ** start connection watchdog **
        setTimeout( 
            ()=>{
                if(this.ws && (this.ws.readyState!=1 && this.ws.readyState!=3) ) { 
                    this.debug(`Connection watchdog expired (${this._wsTimeout/1000} sec): ${this.ws.readyState}... aborting connection...`);
                    this.disconnect(); 
                }
            }, this._wsTimeout
        );
		
		this.ws.onopen= e=> { this.debug(`ws.open`); this._connect.next(e) }
		this.ws.onclose= e=> { this.debug(`ws.close`); this._close.next(e) }
		this.ws.onerror= e=> { this.debug(`ws.error`); this._error.next(e) }
		this.ws.onmessage= e=> {
            let data;
            if(typeof e.data === 'string') {
                try { data= JSON.parse(e.data) }
                catch(e) { return }
            }
            if(this.isHello(data)) {
                this.server.ws.roles= data.roles;
                this.server.ws.self= data.self;
            }          
			if(this._filter && this.isDelta(data)) {
                if(data.context== this._filter) { this._message.next(data) } 
            }
            else { this._message.next(data) }
		}
    }  

    // ** disconnect from Signal K stream
    disconnect() {
        this.ws.close();
        this.ws= null;
        this.server.ws= { self: null, roles: {} };
    }


    // **************** STREAM API ***************************

    // ** send data to Signal K stream
    send(data:any) {
        if(this.ws) {
            if(typeof data === 'object') { data= JSON.stringify(data) }
            this.ws.send(data);
        }
    }

    // ** send value via delta stream update **
    sendUpdate(context:string='self', path:string, value:any) {
        let val= { 
            context: (context=='self') ? 'vessels.self' : context,
            updates: [ {
                values: [{ path: path, value: value }] 
            } ] 
        }
        this.debug(`sending update: ${JSON.stringify(val)}`);
        this.send(val);
    }

    // ** Subscribe to Delta stream messages **
    subscribe(context:string='*', path:string='*', ...options) {
        let data= {
            context: (context=='self') ? 'vessels.self' : context,
            subscribe: []
        };

        let subscription= {};
        subscription['path']= path;
        for(let i in options) {
            switch(i) {
                case '0': 
                    if( !isNaN(options[i]) ) { subscription['period']= parseInt(options[i]) }
                    break;
                case '1': 
                    if( options[i]=='delta' || options[i]=='full') { 
                        subscription['format']= options[i] 
                    }
                    break;
                case '2': 
                    if( options[i]=='instant' || options[i]=='ideal' 
                            || options[i]== 'fixed' ) {
                                subscription['policy']= options[i];
                    }
                    break;     
                case '3': 
                    if(subscription['policy']=='instant') {
                        if( !isNaN(options[i]) ) { subscription['minPeriod']= parseInt(options[i]) } 
                    }
                    break;
            }
        }
        data.subscribe.push(subscription);
        this.send(data);        
    }

    // ** Unsubscribe from Delta stream messages **
    unsubscribe(context:string='*', path:string='*') {
        context= (context=='self') ? 'vessels.self' : context;
        this.send({
            "context": context,
            "unsubscribe": [ {"path": path} ]
        });
    }

    // ** returns true if recieved message is a Delta message
    isDelta(msg) { return typeof msg.context!= 'undefined' }
    // ** returns true if recieved message is a Hello message
    isHello(msg) { return typeof msg.version!= 'undefined' }


    // ** get / set filter to select delta messages just for supplied vessel id   
    get filter(): string { return this._filter }
    // ** set filter= null to remove message filtering
    set filter(id:string) { 
        if(!id) {   // ** clear filter
            this._filter=null;
            return;
        }
        if( id.indexOf('self')!=-1 ) {  // ** self
            if(!this.server.ws.self) {
                this.getSelfId().subscribe( id=> { this._filter= id } );
            }
            else { this._filter= this.server.ws.self }
        }
        else {  // ** uuid
            let uuid= RegExp("^urn:mrn:signalk:uuid:[0-9A-Fa-f]{8}-[0-9A-Fa-f]{4}-4[0-9A-Fa-f]{3}-[89ABab][0-9A-Fa-f]{3}-[0-9A-Fa-f]{12}$")
            if(id.indexOf('vessels.')!=-1) { id= id.slice(id.indexOf('.')+1) }
            if(uuid.test(id)) { this._filter= `vessels.${id}` }
        }
    }

    // ** Raise an alarm message **
    raiseAlarm(context:string='self', alarmPath:string, alarm:Alarm) {
        this.sendUpdate(context, `notifications.${alarmPath}`, alarm);
    }

    // ** Clear alarm **
    clearAlarm(context:string='self', alarmPath:string) {
        this.sendUpdate(context, `notifications.${alarmPath}`, null);
    } 
            

    // **************** HTTP API ***************************
    
    // ** Returns the contents of the Signal K tree pointed to by self
    getSelf() { return this.apiGet(`vessels/self`) }

    // ** Returns the self identity
    getSelfId() { return this.apiGet(`self`) }

    // ** return observable response for meta object at the specified context and path
    getMeta(context:string, path:string) { 
        return this.apiGet(`${this.contextToPath(context)}/${path.split('.').join('/')}/meta`);
    }    

    //** return observable response from http api path
    apiGet(path:string) { 
        let url= this.resolveHttpEndpoint();
        if(!url) { return }
        if(path[0]=='/') { path= path.slice(1) }
        url+= path;
        this.debug(`apiGet ${url}`);

        if(this._token) {
            let headers= new HttpHeaders( { 'Authorization': `JWT ${this._token}` } );
            return this.http.get( url, { headers: headers } );
        }
        else { 
            if(this.server.authRequired) {
                this.debug('Auth Required and NO token available!');
                this._error.next(new Error('Auth Required and NO token available!')); 
            }              
            return this.http.get(url);
        }
    }

    //** Send value to http api path
    apiPut(context:string, path:string, key:any, value:any) { 
        let url= this.resolveHttpEndpoint();
        if(!url) { return }
        if(path[0]=='/') { path= path.slice(1) }
        url+= this.contextToPath(context) + '/' + path;

        let msg = { value: {} } 
        msg.value[key]= value;

        this.debug(`apiPut ${url}`);
        this.debug(JSON.stringify(msg));

        if(this._token) {
            let headers= new HttpHeaders( { 'Authorization': `JWT ${this._token}` } );
            return this.http.put(url, msg, { headers: headers } );
        }        
        else { 
            if(this.server.authRequired) {
                this.debug('Auth Required and NO token available!');
                this._error.next(new Error('Auth Required and NO token available!')); 
            }              
            return this.http.put(url, msg); 
        }
    }    
    
    //** return observable response from http path
    get(path:string) { 
        let url= `${this.protocol}://${this.hostname}:${this.port}${path}`;
        this.debug(`get ${url}`);

        if(this._token) {
            let headers= new HttpHeaders( { 'Authorization': `JWT ${this._token}` } );
            return this.http.get( url, { headers: headers } );
        }
        else { 
            if(this.server.authRequired) {
                this.debug('Auth Required and NO token available!');
                this._error.next(new Error('Auth Required and NO token available!')); 
            }           
            return this.http.get(url);
        }        
    };  

    // ** return url for connected signalk-http endpoint
    private resolveHttpEndpoint() {
        let url;
        if(this.server.endpoints[this._version]) { // ** connection made
            // ** connect to http endpoint at prescribed version else fall back to default version
            if(this.server.endpoints[this._version]['signalk-http']) {
                url= `${this.server.endpoints[this._version]['signalk-http']}`;
            }
            else { url= `${this.server.endpoints['v1']['signalk-http']}` }   
        } 
        else {
            let msg= 'No current connection http endpoint service! Use connect() to establish a connection.'
            this.debug(msg);
            this._error.next(new Error(msg) );
        } 
        return url;   
    }

    // ** parse context to valid Signal K path
    private contextToPath(context:string) {
        let res= (context=='self' ) ? 'vessels.self': context;
        return res.split('.').join('/');
    }

}

// ** Alarm state **
export enum  AlarmState {
    normal='normal',
    alert='alert',
    warn='warn',
    alarm='alarm',
    emergency='emergency'
}

// ** Alarm Method **
export enum  AlarmMethod {
    visual='visual',
    sound='sound'
}

// ** Alarm class
export class Alarm {
    state: AlarmState= AlarmState.normal;
    method: Array<AlarmMethod>= [AlarmMethod.visual, AlarmMethod.sound];
    message: string;

    constructor(message:string=null) { this.message= message }
}


